<template>
  <div id="topBlog">
    <h2 class="mb-6 grey--text" v-if="$route.query.search">نتایج جستجو بر اساس : <span
        class="black--text">"{{ $route.query.search }}"</span></h2>
    <div v-for="(item, index) in data.data" :key="index + 'posts'">
      <router-link :to="{ name: 'SingleBlog', params: { id: item.id, title: item.title }, hash: '#topBlog' }" custom
        v-slot="{ navigate, href }">
        <a :href="href" @click="navigate">
          <v-card elevation="0" :loading="loading" :disabled="loading">
            <PostItem :item="item"></PostItem>
          </v-card>
        </a>
      </router-link>
    </div>

    <h3 v-if="data && data.data && data.data.length === 0" class="text-center my-12">موردی یافت نشد.</h3>

    <div class="text-center my-6">
      <p class="text-center mb-1 primary--text" v-if="data.total_objects">{{ data.total_objects }} مطلب</p>
      <v-pagination v-if="data.total_objects" :value="options.page" v-model="options.page" color="primary"
        :length="data.total_pages"></v-pagination>

    </div>

  </div>
</template>

<script>
import PostItem from "@/components/Blog/Blog/PostItem";
import axios from "axios";
export default {
  created() {
    this.checkQueries();

    if (this.$route.query.tag) {
      this.fetchPostbyTag();
    } else if (this.$route.query.u) {
      this.fetchPostbyUser();
    } else {
      this.fetchPosts();
    }


  },
  name: "PostsBlog",
  data() {
    return {
      data: {},
      loading: false,

      options: {
        page: 1,
        category: null,
        search: null,
        ordering: '-id',
      },
      tag: null,
      user: null,
    }
  },
  components: {
    PostItem
  },
  methods: {
    checkQueries() {
      if (this.$route.query.page) {
        this.options.page = parseInt(this.$route.query.page);
      }

      if (this.$route.params.id) {
        this.options.category = this.$route.params.id;
      }

      if (this.$route.query.search) {
        this.options.search = this.$route.query.search;
      }

      if (this.$route.query.ordering) {
        this.options.ordering = this.$route.query.ordering;
      }

      if (this.$route.query.tag) {
        this.tag = this.$route.query.tag;
      }

      if (this.$route.query.u) {
        this.user = parseInt(this.$route.query.u);
      }
    },

    fetchPosts() {

      this.loading = true;
      axios.get(this.$store.state.host + '/blog/api/v1/post/enabled-posts', {
        params: this.options
      }).then(response => {
        this.data = response.data;
        this.loading = false;
      }).finally(() => {
        this.$store.state.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    },
    fetchPostbyTag() {
      this.loading = true;
      axios.get(this.$store.state.host + '/blog/api/v1/post/by-tag/' + this.tag).then(response => {
        this.data = response.data;
        this.loading = false;
      }).finally(() => {
        this.$store.state.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    },
    fetchPostbyUser() {
      this.loading = true;
      axios.get(this.$store.state.host + '/blog/api/v1/post/by-user/' + this.user).then(response => {
        this.data = response.data;
        this.loading = false;
      }).finally(() => {
        this.$store.state.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    },
  },
  watch: {
    '$route'() {
      this.checkQueries();
      if (this.$route.query.tag) {
        this.fetchPostbyTag();
      } else if (this.$route.query.u) {
        this.fetchPostbyUser();
      } else {
        this.fetchPosts();
      }
    },
    'options.page'(val) {
      this.$router.push({ query: { page: val } }).catch(() => { });
    }
  }
}
//HeaderInja
</script>

